// This code is to animate the first image on page as it loads.
@media screen and (min-width: 736px) {
  html {
    #caslink-wrapper {
      section {
        .imac-monitoring {
          opacity: 0;
          top: -40px;
          -webkit-transition: all 1s ease-out;
          -moz-transition: all 1s ease-out;
          -o-transition: background-all 1s ease-out;
          transition: all 1s ease-out;
        }

        .imac-monitoring.loaded {
          opacity: 1;
          top: 0px;
        }
      }
    }
  }
}

// This code is to style and animate the easy installation section
@media (min-width: 736px) {
  html {
    #caslink-wrapper {
      section.installation {
        .graphic-container {
          max-height: 213.766px;

          .easy-installation-icon {
            .transmission-circle {
              -webkit-animation: transmission-change .5s infinite linear both;
              animation: transmission-change .5s infinite linear both;
            }
          }
        }
      }
    }
  }
}

// This code animates the buttons for the slider
#caslink-wrapper {
  section.interface {
    .flex-direction-nav {
      a.flex-next,
      a.flex-prev {
        &:hover {
          transform: scale(1.3);
          -webkit-transform: scale(1.3);
          -moz-transform: scale(1.3);
        }
      }
    }
  }
}

// Below is for all of the animations on the initial 4 images.
#thermometer-icon-container {
  .thermometer-icon {
    .thermometer-fill {
      height: 37%;
      y: 147;
    }

    .thermometer-fill-active {
      -webkit-animation: temperature-change 1.4s 1 ease-in-out both;
      animation: temperature-change 1.4s 1 ease-in-out both;
    }
  }
}

#tachometer-icon-container {
  .tachometer-icon {
    .tachometer-needle {
      -webkit-transform: rotate(30deg);
      -webkit-transform-origin: 40% 54%;
      transform: rotate(30deg);
      transform-origin: 40% 54%;
    }

    .tachometer-needle-active {
      -webkit-animation: tachometer-change 1.4s 1 ease-in-out both;
      animation: tachometer-change 1.4s 1 ease-in-out both;
    }
  }
}

#ventilation-icon-container {
  .ventilation-icon {
    .ventilation-fumes-1 {
      -webkit-transform: translate(81%, 0%);
      transform: translate(81%, 0%);
    }

    .ventilation-fumes-2 {
      -webkit-transform: translate(97%, 0%);
      transform: translate(97%, 0%);
    }

    .ventilation-fumes-3 {
      -webkit-transform: translate(111%, 0%);
      transform: translate(111%, 0%);
    }

    .ventilation-fumes-active-1 {
      -webkit-animation: ventilation-change-1 1.4s 1 ease-in-out both;
      animation: ventilation-change-1 1.4s 1 ease-in-out both;
    }

    .ventilation-fumes-active-2 {
      -webkit-animation: ventilation-change-2 1.4s 1 ease-in-out both;
      -webkit-animation-delay: 0.16s;
      animation: ventilation-change-2 1.4s 1 ease-in-out both;
      animation-delay: 0.16s;
    }

    .ventilation-fumes-active-3 {
      -webkit-animation: ventilation-change-3 1.4s 1 ease-in-out both;
      -webkit-animation-delay: 0.32s;
      animation: ventilation-change-3 1.4s 1 ease-in-out both;
      animation-delay: 0.32s;
    }
  }
}

#light-icon-container {
  .light-icon {
    .light-beam {
      opacity: 0;
    }

    .light-beam-active {
      -webkit-animation: light-change 1.4s 1 ease-in-out both;
      animation: light-change 1.4s 1 ease-in-out both;
    }
  }
}

// Define the keyframe animation for the thermometer svg.
@-webkit-keyframes temperature-change {
  0% {
    y: 147;
  }
  33% {
    y: 142;
  }
  66% {
    y: 153;
  }
  100% {
    y: 147;
  }
}

@keyframes temperature-change {
  0% {
    y: 147;
  }
  33% {
    y: 142;
  }
  66% {
    y: 153;
  }
  100% {
    y: 147;
  }
}

// Define the keyframe animation for the tachometer svg.
@-webkit-keyframes tachometer-change {
  0% {
    -webkit-transform: rotate(30deg);
  }
  33% {
    -webkit-transform: rotate(10deg);
  }
  66% {
    -webkit-transform: rotate(61deg);
  }
  100% {
    -webkit-transform: rotate(30deg);
  }
}

@keyframes tachometer-change {
  0% {
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg);
  }
  33% {
    -webkit-transform: rotate(10deg);
    transform: rotate(10deg);
  }
  66% {
    -webkit-transform: rotate(61deg);
    transform: rotate(61deg);
  }
  100% {
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg);
  }
}

// Define the keyframe animation for the ventilation svg.
@-webkit-keyframes ventilation-change-1 {
  0% {
    -webkit-transform: translate(81%, 0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(81%, -34%);
    opacity: 0;
  }
}

@-webkit-keyframes ventilation-change-2 {
  0% {
    -webkit-transform: translate(97%, 0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(97%, -34%);
    opacity: 0;
  }
}

@-webkit-keyframes ventilation-change-3 {
  0% {
    -webkit-transform: translate(111%, 0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(111%, -34%);
    opacity: 0;
  }
}

@keyframes ventilation-change-1 {
  0% {
    -webkit-transform: translate(81%, 0%);
    transform: translate(81%, 0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(81%, -34%);
    transform: translate(81%, -34%);
    opacity: 0;
  }
}

@keyframes ventilation-change-2 {
  0% {
    -webkit-transform: translate(97%, 0%);
    transform: translate(97%, 0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(97%, -34%);
    transform: translate(97%, -34%);
    opacity: 0;
  }
}

@keyframes ventilation-change-3 {
  0% {
    -webkit-transform: translate(111%, 0%);
    transform: translate(111%, 0%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translate(111%, -34%);
    transform: translate(111%, -34%);
    opacity: 0;
  }
}

// Define the keyframe animation for the light svg.
@-webkit-keyframes light-change {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes light-change {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


// Define the keyframe animation for the easy installation svg.
@-webkit-keyframes transmission-change {
  0% {
    -webkit-transform: translateX(0%);
  }
  100% {
    -webkit-transform: translateX(2.55%);
  }
}
@keyframes transmission-change {
  0% {
    -webkit-transform: translateX(0%);
    transform: translateX(0%);
  }
  100% {
    -webkit-transform: translateX(2.55%);
    transform: translateX(2.55%);
  }
}